.glyph {
  float: left;
  text-align: center;
  padding: .75em;
  margin: .4em 1.5em .75em 0;
  width: 6em;
  text-shadow: none; }

.glyph_big {
  font-size: 128px;
  color: #59c5dc;
  float: left;
  margin-right: 20px; }

.glyph div {
  padding-bottom: 10px; }

.glyph input {
  font-family: consolas, monospace;
  font-size: 12px;
  width: 100%;
  text-align: center;
  border: 0;
  box-shadow: 0 0 0 1px #ccc;
  padding: .2em;
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px; }

.centered {
  margin-left: auto;
  margin-right: auto; }

.glyph .fs1 {
  font-size: 2em; }

.toast-title {
  font-weight: 700; }

.toast-message {
  -ms-word-wrap: break-word;
  word-wrap: break-word; }

.toast-message a,
.toast-message label {
  color: #FFF; }

.toast-message a:hover {
  color: #CCC;
  text-decoration: none; }

.toast-close-button {
  position: relative;
  right: -.3em;
  top: -.3em;
  float: right;
  font-size: 20px;
  font-weight: 700;
  color: #FFF;
  -webkit-text-shadow: 0 1px 0 #fff;
  text-shadow: 0 1px 0 #fff;
  opacity: .8;
  filter: alpha(opacity=80);
  line-height: 1; }

.toast-close-button:focus,
.toast-close-button:hover {
  color: #000;
  text-decoration: none;
  cursor: pointer;
  opacity: .4;
  filter: alpha(opacity=40); }

.rtl .toast-close-button {
  left: -.3em;
  float: left;
  right: .3em; }

button.toast-close-button {
  padding: 0;
  cursor: pointer;
  background: 0 0;
  border: 0;
  -webkit-appearance: none; }

.toast-top-center {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%; }

.toast-bottom-center {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 100%; }

.toast-top-full-width {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%; }

.toast-bottom-full-width {
  position: fixed;
  bottom: 0;
  right: 0;
  width: 100%; }

.toast-top-left {
  position: fixed;
  top: 12px;
  left: 12px; }

.toast-top-right {
  position: fixed;
  top: 12px;
  right: 12px; }

.toast-bottom-right {
  position: fixed;
  right: 12px;
  bottom: 12px; }

.toast-bottom-left {
  position: fixed;
  bottom: 12px;
  left: 12px; }

.error-mesage__container {
  position: relative;
  width: 100%; }

.toast-bottom-center-element {
  bottom: -100px;
  left: 50%;
  width: auto;
  position: absolute;
  overflow: hidden;
  display: inline-block;
  transform: translateX(-50%); }

#toast-container {
  z-index: 999999;
  pointer-events: none; }

#toast-container * {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-size: 14px;
  text-align: center; }

